import { Injectable } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

@Injectable()
export class ToolbarService {
  public title: string = "";
  public groups: any[];
  public current: any;

  constructor() {
    this.groups = [
      { 
        title: 'Услуги',
        name: 'services',
        items: [
          {route: '/services', name: 'list', title: "Список услуг"},
          {route: '/services/add', name: 'add', title: "Добавить услугу"}
        ],
        add: '/services/add'
      },
      { 
        title: 'Врачи',
        name: 'doctors',
        items: [
          {route: '/doctors', name: 'list', title: "Список врачей"},
          {route: '/doctors/add', name: 'add', title: "Добавить врача"}
        ],
        add: '/doctors/add'
      }
    ];
  }

  change(title: string, route?: string) {
    let self = this;
    let params = route ? route.split('.') : [];
    this.title = title;
    this.current = null;

    if(route) {
      this.groups.find((group) => {
        if(group.name == params[0]) {
          self.current = group;
          if(params[1]) group.items.find((item) => {
            if(item.name == params[1]) self.current = item;
          });
          return true;
        }
      });
    }
  }

}



